// 

import request from "@/utils/request.js"

export default {
	// id
	getList(data) {
		return request.post('/applet/article/list', data);
	},
	// 
	getPage(data) {
		return request.post('/applet/article/page', data);
	},
	// 
	getDetail(data) {
		return request.post('/applet/article/detail', data);
	}
}