// 

import request from "@/utils/request.js"

export default {
	// id
	getList(data){
		return request.post('/applet/video/list',data);
	},
	// 
	getPage(data){
		return request.post('/applet/video/page',data);
	},
	// 
	getVideoDetail(data) {
		return request.post('/applet/video/detail',data);
	}
}